import React from 'react';
import { Themeguide } from '../headers/Themeguide.jsx';
import '../styles/Step3.css';
import {InitializeLocalStorage} from "../function/InitializeLocalStorage";

function Step3() {

    const goToInitialPage = () => {
        InitializeLocalStorage();
        window.location.href = "/";
    }



    return (
        <div className='wrap'>
            <Themeguide />
            <div className='background'>
                <div className='download-message'>
                    <div> -</div>
                    <div><span style={{ color: '#E78D08' }}> </span> !</div>
                </div>
                <div className='remake-button' onClick={goToInitialPage}>  </div>
            </div>
        </div>



    );
}
export default Step3;